<template>
  <div class="container">
    <header class="jumbotron">
      <h3>{{ content }}</h3>
    </header>
    <vue-good-table :columns="columns" :rows="rows" />
  </div>
</template>

<script>
import { inject } from "vue";
import UserService from "../services/user.service";
import EventBus from "../common/EventBus";

export default {
  name: "Admin",
  data() {
    return {
      columns: [
        {
          label: "Name",
          field: "membername",
        },
        {
          label: "Email",
          field: "email",
        },
        {
          label: "Birthdate",
          field: "birthdate",
        },
        {
          label: "Phone Number",
          field: "phoneNumber",
        },
      ],
      rows: [],
      content: "",
    };
  },
  methods: {
    getAllUsers() {
      const toast = inject("toast", "");

      UserService.getAll()
      .then((response) => {
        this.rows = response.data.data.dtos;
        return UserService.getAdminBoard();
      }).then((response) => {
        this.content = response.data;
      }).catch((error) => {
        toast.value =
            (error.response &&
              error.response.data &&
              error.response.data.message) ||
            error.message ||
            error.toString();

          if (error.response && error.response.status === 403) {
            EventBus.dispatch("logout");
          }
      });
    },
  },
  mounted() {
    this.getAllUsers();
  },
};
</script>
